/* detail page  */
.detailsCom{
  background-image: linear-gradient(rgba(38, 156, 221, 0.2), white);
}
.details{
  width: 100%;
  margin: 0px auto;
  display: flex;
  flex-direction: column;
}
.mainImgBox{
  width: 100%;
  /* height: 45vh; */
}
.mainImg{
  width: 100%;
  border-radius: 5px;
}
.subImgs{
  margin: 5px;
  width: 4.5rem;
  height: 4.5rem;
  border-radius: 5px;
  cursor: pointer;
}
.detailsBoxTwo{
  background-color: white;
  border-radius: 30px;
  margin-top: 15px;
  padding: 20px;
}
.detailIcons{
  display: flex;
  justify-content: end;
  align-items: center;
}
.detailIcon{
  width: 1.7rem;
  cursor: pointer;
  margin: 10px;
}
.detailBtn{
 padding: 0px 15px;
 height: 35px;
 /* font-size: .9rem; */
 margin-left: 10px;
}
.detailHeading{
  padding-top: 10px;
  font-size: 1.5rem;
}
.deailTable{
  width: 100%;
  margin: 0 auto;
}
.deailTable tr{
  /* height: 40px; */
  font-family: 'Rubik', sans-serif;
  font-size: 14px;
} 
.deailTable tr th{
  padding-top: 10px;
  padding-bottom: 10px;
  text-align: left;
  padding-left: 30px;
  font-weight: 500;

} 

.deailTable td{
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 30px;
}

.deailTable tr:nth-child(odd){
  background-color: #DEF0FA;
}
.deailTable tr:nth-child(even){
  background-color: #fffefe;
}
.moreDetailBox{
  display: flex;
  gap: 10px;
  margin:20px 0px;
  flex-wrap: wrap;
}
.moreDetail{
 font-size: 13px;
 color: #269CDD;
 padding: 8px 15px;
 background-color: #DEF0FA;
 border-radius: 20px;
 font-weight: 500;
}
.detailContent{
  font-family: 'Roboto', sans-serif;  font-size: 15px;
  font-size: 15px;
  line-height: 27px;
  letter-spacing: .2px;
  color: #6b6b6b;

}
@media (min-width: 600px) {
  .deailTable{
    width: 60%;
    margin: 0px;

  }
}

@media (min-width: 1100px) {
  .details{
    flex-direction: row;
    justify-content: space-evenly;
    padding-top: 30px;  
  }
  .detailsBoxOne{
    width: 45%;
  }
  .detailsBoxTwo{
    width: 45%;
    margin-top: 0px;
    padding: 50px;
  }
  .deailTable{
    width: 50%;
    margin: 0px;
  }
  .detailHeading{
    justify-content: start;
    font-size: 2rem;
    margin-bottom: 2rem;
  }
  .detailBtn{
    font-size: 1rem;
    padding: 0px 20px;

  }
  .detailIcon{
    width: 2rem;
  }
  
}

